// src/pages/GalleryUpload.jsx
import React, { useState, useContext } from 'react';
import { getFirestore, collection, addDoc, serverTimestamp } from 'firebase/firestore';
import { getStorage, ref, uploadBytes, getDownloadURL } from 'firebase/storage';
import { useNavigate } from 'react-router-dom';
import { UserInfoContext } from '../contexts/UserInfoContext';
import imageCompression from 'browser-image-compression';

export default function GalleryUpload() {
  const { user } = useContext(UserInfoContext);
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [hashtags, setHashtags] = useState('');
  const [isPublic, setIsPublic] = useState(true);
  const [imageFiles, setImageFiles] = useState([]);
  const [uploading, setUploading] = useState(false);
  const navigate = useNavigate();
  const db = getFirestore();
  const storage = getStorage();

  const handleImageChange = (e) => {
    const files = Array.from(e.target.files);
    if (files.length > 20) {
      alert('최대 20장까지만 업로드할 수 있습니다.');
      return;
    }
    setImageFiles(files);
  };

  const resizeImage = async (file) => {
    const options = {
      maxSizeMB: 1.5,
      maxWidthOrHeight: 1024,
      useWebWorker: true,
    };
    try {
      const compressedFile = await imageCompression(file, options);
      return compressedFile;
    } catch (error) {
      console.error('이미지 리사이징 실패:', error);
      return file;
    }
  };

  const handleUpload = async (e) => {
    e.preventDefault();
    if (!user) return alert('로그인이 필요합니다.');
    if (!title || !imageFiles.length) {
      return alert('제목과 이미지를 입력해주세요.');
    }

    setUploading(true);
    try {
      const imageUrls = [];
      const imagePaths = [];

      for (const file of imageFiles) {
        const resized = await resizeImage(file);
        const fileName = `${Date.now()}-${file.name}`;
        const storagePath = `gallery/${user.uid}/${fileName}`;
        const storageRef = ref(storage, storagePath);

        await uploadBytes(storageRef, resized);
        const downloadURL = await getDownloadURL(storageRef);

        imageUrls.push(downloadURL);
        imagePaths.push(storagePath);
      }

      const hashtagArray = hashtags
        .split(',')
        .map(tag => tag.trim())
        .filter(tag => tag !== '');

      await addDoc(collection(db, 'gallery'), {
        title,
        description,
        url: imageUrls[0],
        images: imageUrls,
        imagePath: imagePaths[0], // 🔑 대표 이미지 경로 저장
        isPublic,
        hashtags: hashtagArray,
        author: user.email,
        createdAt: serverTimestamp()
      });

      alert('업로드 완료!');
      navigate('/gallery');
    } catch (error) {
      console.error('업로드 실패:', error);
      alert('업로드 중 문제가 발생했습니다.');
    } finally {
      setUploading(false);
    }
  };

  return (
    <div className="max-w-2xl mx-auto mt-10 bg-white p-6 shadow rounded">
      <h2 className="text-2xl font-bold mb-4 text-sky-700">🖼️ 이미지 업로드</h2>
      <form onSubmit={handleUpload} className="space-y-4">
        <input
          type="file"
          accept="image/*"
          multiple
          onChange={handleImageChange}
          required
        />
        <p className="text-xs text-gray-500">최대 20장, 1.5MB 이하로 자동 압축됩니다.</p>

        <input
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          placeholder="제목"
          className="w-full border rounded p-2"
          required
        />
        <textarea
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          placeholder="설명 (선택)"
          className="w-full border rounded p-2 min-h-[100px]"
        />
        <input
          type="text"
          value={hashtags}
          onChange={(e) => setHashtags(e.target.value)}
          placeholder="해시태그 입력 (예: 감성, 바다, 여행)"
          className="w-full border rounded p-2"
        />
        <label className="flex items-center gap-2 text-sm">
          <input
            type="checkbox"
            checked={isPublic}
            onChange={(e) => setIsPublic(e.target.checked)}
          />
          공개 여부
        </label>
        <button
          type="submit"
          disabled={uploading}
          className="bg-sky-600 hover:bg-sky-700 text-white px-4 py-2 rounded"
        >
          {uploading ? '업로드 중...' : '업로드'}
        </button>
      </form>
    </div>
  );
}
