import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { useState, useEffect } from 'react';
import { onAuthStateChanged } from 'firebase/auth';

import { auth } from './firebase';
import { UserInfoProvider } from './contexts/UserInfoContext';
import LanguageContext from './contexts/LanguageContext';

import Navbar from './components/Navbar';

import Home from './pages/Home';
import Gallery from './pages/Gallery';
import GalleryUpload from './pages/GalleryUpload'; // ✅ 추가된 import

import CommunityList from './pages/CommunityList';
import CommunityWrite from './pages/CommunityWrite';
import PostDetail from './pages/PostDetail';
import InstructorBoard from './pages/InstructorBoard';
import TourList from './pages/TourList';
import TourUpload from './pages/TourUpload';
import Login from './pages/Login';
import Signup from './pages/Signup';

// 추가된 페이지 컴포넌트 import
import FreeBoard from './pages/community/Freeboard';  // 자유게시판 페이지
import TourRequest from './pages/community/TourRequest';  // 투어신청 페이지
import Qna from './pages/community/Qna';  // Q&A 페이지

import './index.css';

function App() {
  const [language, setLanguage] = useState('en');
  const [user, setUser] = useState(null);

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (currentUser) => {
      setUser(currentUser);
    });
    return () => unsubscribe();
  }, []);

  return (
    <LanguageContext.Provider value={{ language, setLanguage }}>
      <UserInfoProvider>
        <Router>
          <div className="min-h-screen bg-gradient-to-b from-sky-100 to-white text-gray-800">
            <Navbar />
            <Routes>
              <Route path="/" element={<Home />} />
              <Route path="/gallery" element={<Gallery />} />
              <Route path="/gallery/upload" element={<GalleryUpload />} /> {/* ✅ 새 라우트 */}
              <Route path="/gallery/:id" element={<PostDetail />} /> {/* 상세 페이지 라우트 추가 */}
              <Route path="/community" element={<CommunityList />} />
              <Route path="/community/new" element={<CommunityWrite />} />
              <Route path="/community/:id" element={<PostDetail />} />
              <Route path="/community/free" element={<FreeBoard />} />  {/* 자유게시판 */}
              <Route path="/community/tour" element={<TourRequest />} /> {/* 투어신청 */}
              <Route path="/community/qna" element={<Qna />} />  {/* Q&A */}
              <Route path="/instructors" element={<InstructorBoard />} />
              <Route path="/tours" element={<TourList />} />
              <Route path="/tours/upload" element={<TourUpload />} />
              <Route path="/login" element={<Login />} />
              <Route path="/signup" element={<Signup />} />
            </Routes>
          </div>
        </Router>
      </UserInfoProvider>
    </LanguageContext.Provider>
  );
}

export default App;
